<template>
  <div class="scene-box">
    <div id="canvas"></div>
    <div class="change-btn">
      <!-- <div @click="changeAction(0)">动画1</div>
    <div @click="changeAction(1)">动画2</div>
    <div @click="changeAction(2)">动画3</div> -->
      <!-- <div>{{ store.state.changeEventType.eventName }}</div>
      <div @click="changeType('Play')">Play</div>
      <div @click="changeType('Stop')">Stop</div> -->
    </div>
  </div>
</template>
<script lang="ts" setup>
import * as THREE from "three";
import { onMounted, watch } from "vue";
import { GLTFLoader } from "three/examples/jsm/loaders/GLTFLoader.js";
import { OrbitControls } from "three/examples/jsm/controls/OrbitControls.js";
import { DRACOLoader } from "three/examples/jsm/loaders/DRACOLoader.js";
import store from "@/store";

let clock = new THREE.Clock();
let actionList: any = [];
let currentAction: any = null;
let i = 1;
let timer: any = null;

const getDracoLoader = () => {
  //对模型解压
  const dracoloader = new DRACOLoader();
  dracoloader.setDecoderPath("/draco/");
  dracoloader.setDecoderConfig({ type: "js" });
  dracoloader.preload();
  return dracoloader;
};

// const changeAction = (index: number) => {
//   // currentAction && currentAction.stop();
//   // actionList[index].play();
//   currentAction.crossFadeTo(actionList[index], 1.0, true);
//   // currentAction.stop();
//   currentAction = actionList[index];
//   currentAction.stop();
//   currentAction.play();
// };

const changeType = (eventName: string) => {
  const changeEventType = { ...store.state.changeEventType };
  changeEventType.eventName = eventName;
  store.state.changeEventType = { ...changeEventType };
};

const changeAction = (index: number) => {
  // currentAction.crossFadeTo(actionList[index], 1.0);
  // currentAction.stop();
  if (currentAction != actionList[index]) {
    currentAction.fadeOut(1);
  }
  actionList[index]
    .reset()
    .setEffectiveTimeScale(1)
    .setEffectiveWeight(1)
    .fadeIn(1)
    .play();
  // actionList[index].crossFadeFrom(currentAction, 1.0, true).play();
  // console.log(actionList[index], 222, currentAction);
  currentAction = actionList[index];
  //currentAction.stop();
  // currentAction.play();
  // actionList[index].fadeIn(2).play();
};

onMounted(() => {
  const width = window.innerWidth,
    height = window.innerHeight;
  // init

  const camera = new THREE.PerspectiveCamera(
    25,
    window.innerWidth / window.innerHeight,
    0.1,
    1000
  );
  camera.position.z = 10;

  const scene = new THREE.Scene();

  const geometry = new THREE.BoxGeometry(0.2, 0.2, 0.2);
  const material = new THREE.MeshNormalMaterial();

  const mesh = new THREE.Mesh(geometry, material);
  // scene.add(mesh);

  const renderer = new THREE.WebGLRenderer({ antialias: true });
  renderer.setSize(width, height);
  renderer.setClearColor(0xeeeeee);
  // renderer.setAnimationLoop(animate);
  (document.querySelector("#canvas") as any).appendChild(renderer.domElement);

  // 添加OrbitControls，允许用户通过鼠标移动来 orbits the camera around the object
  const controls = new OrbitControls(camera, renderer.domElement);
  controls.enabled = false; // 页面禁止转动
  controls.enableRotate = false;

  // 添加平行光源
  const directionalLight = new THREE.DirectionalLight(0xffffff, 1);
  directionalLight.position.set(0, 2, 3).normalize();
  scene.add(directionalLight);
  const light = new THREE.AmbientLight(0xffffff, 3); // 柔和的白光
  scene.add(light);
  // 加载GLTF模型
  const loader = new GLTFLoader();
  const dracoloader = getDracoLoader();
  loader.setDRACOLoader(dracoloader); //注入loader
  loader.load(
    "/body_anim_1212.glb",
    (gltf: any) => {
      gltf.scene.position.set(0, -2, 0);
      gltf.scene.scale.set(2.35, 2.35, 2.35);
      scene.add(gltf.scene);

      // 获取模型的动画
      const animations = gltf.animations;

      // 创建 AnimationMixer
      const mixer = new THREE.AnimationMixer(gltf.scene);
      console.log(animations);
      // 创建 AnimationAction
      const action1 = mixer.clipAction(animations[2]);
      const action2 = mixer.clipAction(animations[0]);
      const action3 = mixer.clipAction(animations[1]);
      const action4 = mixer.clipAction(animations[3]);

      // 开始播放动画
      action2.timeScale = 1;
      action2.loop = THREE.LoopOnce;
      action2.clampWhenFinished = true;
      action3.timeScale = 1;
      action3.loop = THREE.LoopOnce;
      action3.clampWhenFinished = true;
      action4.timeScale = 1;
      action4.loop = THREE.LoopOnce;
      action4.clampWhenFinished = true;
      action2.play();
      currentAction = action2;
      actionList = [action1, action2, action3, action4];

      mixer.addEventListener("finished", () => {
        const eventType = store.state.changeEventType.eventName;
        if (eventType == "Play") {
          changeEvents();
        } else if (eventType == "Stop") {
          if (i) {
            changeAction(0);
            i = 0;
          }
        }
      });
      // 在渲染循环中更新动画
      let previousTime = performance.now();
      function animate() {
        requestAnimationFrame(animate);

        // 计算当前时间与上次渲染之间的时间差
        const currentTime = performance.now();
        const deltaTime = (currentTime - previousTime) / 1000;
        previousTime = currentTime;

        // 更新动画
        mixer.update(deltaTime); // deltaTime 是当前帧与前一帧之间的时间差

        renderer.render(scene, camera);
        if (renderer.domElement.style.width == "0px") {
          resizeCanvas(renderer, camera);
        }
      }
      animate();
    },
    undefined,
    (error) => {
      console.error(error);
    }
  );
  window.addEventListener(
    "resize",
    () => {
      resizeCanvas(renderer, camera);
    },
    false
  );
});

const resizeCanvas = (renderer: any, camera: any) => {
  renderer.setSize(window.innerWidth, window.innerHeight);
  camera.aspect = window.innerWidth / window.innerHeight;
  camera.updateProjectionMatrix();
};

const changeEvents = () => {
  if (i == 1) {
    changeAction(0);
    clearTimeout(timer);
    timer = setTimeout(() => {
      changeAction(2);
      i = 2;
    }, 1000);
    i = 0;
  } else if (i == 2) {
    changeAction(0);
    clearTimeout(timer);
    timer = setTimeout(() => {
      changeAction(3);
      i = 3;
    }, 1000);
    i = 0;
  } else if (i == 3) {
    changeAction(0);
    clearTimeout(timer);
    timer = setTimeout(() => {
      changeAction(1);
      i = 1;
    }, 1000);
    i = 0;
  } else {
    changeAction(1);
    i = 1;
  }
};

watch(
  () => store.state.changeEventType,
  (newState: any) => {
    if (newState?.eventName == "Play") {
      changeEvents();
    }
    if (newState?.eventName == "Stop") {
      if (i) {
        changeAction(0);
        i = 0;
      }
    }
  }
);
</script>
<style scoped lang="less">
.scene-box {
  width: 100%;
  height: 100%;
}

.change-btn {
  position: fixed;
  left: 30px;
  top: 60px;

  & > div {
    line-height: 100px;
    cursor: pointer;
  }
}
</style>
