import { createStore } from "vuex";

export default createStore({
  state: {
    name: "xdd",
    changeEventType: {
      type: "EventType",
      eventName: "Play",
    },
  },
  getters: {},
  mutations: {
    resetEditData(state) {
      state.changeEventType = {
        type: "EventType",
        eventName: "Play",
      };
    },
  },
  actions: {
    resetEditData({ commit }) {
      commit("resetEditData");
    },
  },
  modules: {},
});
